  --font-color: #fff1f2;
  --right-background-color: #881337;
  --left-panel-background-color: #630a26;
  --header-footer-background-color: #4c0519;
  --kaleji: #500724;
}
html {
  line-height: 1.15; /* 1 */
  -webkit-text-size-adjust: 100%; /* 2 */
}
html,
body {
  height: 100%;
}
body {
  display: flex;
  flex-direction: column;
  margin: 0;
  color: var(--font-color);
}
header {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 5px;
  background-color: var(--header-footer-background-color);
  color: var(--font-color);
  font-size: larger;
}
header > :first-child {
  font-size: 32px;
  font-weight: 900;
}
.content {
  flex-grow: 1;
  display: grid;
  grid-template-columns: 1fr 4fr;
}
#left-panel {
  padding-left: 10px;
  background-color: var(--left-panel-background-color);
  color: var(--font-color);
}
.home > h2,
.project > h2 {
  margin-bottom: 0;
}
#home > div:hover,
.project-collection > :hover {
  background-color: #3a0111;
}
.delete-project:hover,
.delete-task:hover {
  color: rgb(175, 4, 4);
}
.project-icon-and-text-div {
  gap: 5px;
}
.project-icon-and-text-div + div {
  padding-right: 7px;
}
/* #project-form {
} */
#project-form > .input-field {
  display: flex;
  flex-direction: column;
  align-items: center;
}
input[type='text'],
textarea,
input[type='date'] {
  width: 100%;
  box-sizing: border-box;
  font-family: 'Alata';
  font-size: 1rem;
  background-color: white;
  border: solid 2px #273746;
  border-radius: 3px;
  padding: 5px;
  resize: none;
}
.form-buttons {
  padding: 5px;
  display: flex;
  gap: 5px;
  width: 100%;
}
.project-submit-button,
.task-submit-button {
  /* //input[type='submit'] */
  border: 1px solid #99dd99;
  background-color: forestgreen;
  flex-grow: 1;
  padding: 5px;
  color: var(--font-color);
  font-weight: bold;
}
.cancel-button {
  border: 1px solid #eeadad;
  background-color: darkred;
  flex-grow: 1;
  padding: 5px;
  color: var(--font-color);
  font-weight: bold;
}
.right-panel {
  background-color: var(--right-background-color);
}
.right-panel > h1 {
  padding-left: 20px;
}
.title,
.home-title {
  display: flex;
  /* flex-direction: column; */
  /* justify-content: center; */
  align-items: center;
  cursor: pointer;
  margin: 10px 0 10px 0;
  padding: 5px;
  gap: 5px;
}
.task-div {
  display: flex;
  justify-content: space-between !important;
  padding: 20px;
  border-bottom: 1px solid var(--kaleji);
  transition: 0.3s;
}

.task-div > div {
  display: flex;
  align-items: center;
  gap: 10px;
}
.task-div:hover {
  box-shadow: inset 0 -60px 0 0 var(--kaleji);
  transition: 0.3s;
}
#add-list,
#add-project {
  margin: auto;
  margin-top: 10px;
  font-weight: bold;
  color: var(--font-color);
  background-color: #881337;
  border: none;
  border-radius: 45px;
  box-shadow: 0px 8px 15px rgb(0 0 0 / 54%);
  transition: all 0.3s ease 0s;
}
#add-project {
  background-color: #630a26;
}
button:hover {
  transform: translateY(-7px);
}
.project button {
  border: 0;
  background-color: var(--left-panel-background-color);
  color: var(--font-color);
}
#list-form {
  display: flex;
  justify-content: center;
  flex: 1;
  align-items: flex-start;
  margin: auto;
  font-weight: bold;
  padding: 20px;
  margin-top: 10px;
  background-color: var(--left-panel-background-color);
  opacity: 1;
  pointer-events: auto;
  position: relative;
  transition-delay: 1s;
  max-height: 300px;
  max-width: 550px;
  transition: max-height 0.8s ease-out, opacity 300ms ease-in-out;
  box-shadow: 0px 8px 15px rgb(0 0 0 / 54%);
  transition: all 0.3s ease 0s;
}

footer {
  text-align: center;
  font-size: large;
  padding: 10px;
  background-color: var(--header-footer-background-color);
  color: var(--font-color);
}

/* miscelaneous */
.hidden {
  display: none !important;
}

flex {
  display: flex;
}

.unchecked {
  border: 2px solid red;
  border-radius: 50%;
}
/* left side */
.link,
.project-link {
  display: flex;
  margin-bottom: 15px;
  padding: 12px 20px;
  font-size: 17px;
  text-decoration: none;
  border-radius: 5px;
  transition: 0.3s;
}

a[href] {
  padding: 0;
  margin: 0;
}

.project-icon-and-text-div {
  display: flex;
  justify-content: center;
  margin-bottom: 10px;
  align-items: center;
  color: var(--font-color);
}
.project-trash-icons-div {
  margin-left: auto;
  color: var(--font-color);
}

p {
  margin: 0;
}
.hover-element:hover {
  cursor: pointer;
}
block {
  display: block;
}
.task-done-text {
  text-decoration-line: line-through;
}

@media all and (min-width: 1100px) {
  .task-div {
    padding-right: 200px;
  }
}
.active {
  background-color: #3a0111;
}
